import React, { useState } from 'react';
import { useMediaQuery } from 'react-responsive';

import './Navigation.css';

function Navigation() {

  let mobile = useMediaQuery({maxWidth: 700});
  const [open, setOpen] = useState(!mobile)
  const navItem = (text: string, linkId: string) => {
    return <a className="Nav-Link" href={`#${linkId}`} key={text}>{text}</a>
  }

  const sections = [
    {
      text: 'Hours',
      linkId: 'Hours'
    },
    {
      text: 'Location',
      linkId: 'Address'
    },
    {
      text: 'About',
      linkId: 'About'
    },
  ];
  return (
    <nav id="Navigation">
      <div className={`Navigation-Container ${open && 'Open'}`}>
        {open && sections.map(s => navItem(s.text, s.linkId))}
        { mobile && <button id='NavigationToggle' onClick={e => setOpen(!open)}><img className='Hamburger' alt="Open Navigation" src="data:image/png;base64,"/></button>}
      </div>
    </nav>
  );
}

export default Navigation;
